*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}
.container{
    width: 1000px !important;
}
::-webkit-scrollbar{
    background:pink;
}
@media (max-width:830px){
    .container{
        width: 780px;
    }
}
@media (max-width:770px){
    .container{
        width: 680px;
    }
}
@media (max-width:500px){
    .container{
        width: none;
    }
}